import ordersApi from '@/api/orders';
export default {
    asyncData({params, error}) {
       return ordersApi.getById(params.oid).then(response => {
           return {
               order : response.data.data.item
           }
       })
    },
    methods:{
        //去支付
        toPay(){
            this.$router.push({path:'/pay/'+this.order.orderNo})
        }
    }
}
